:root {
  --strong-cyan: hsl(172, 67%, 45%);
  --very-dark-cyan: hsl(183, 100%, 15%);
  --dark-grayish-cyan: hsl(186, 14%, 43%);
  --darker-grayish-cyan: hsl(184, 14%, 56%);
  --light-grayish-cyan: hsl(185, 41%, 84%);
  --lighter-grayish-cyan: hsl(189, 41%, 97%);
  --white: hsl(0, 0%, 100%);
}

:root {
  --space-mono: "Space Mono", monospace;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--space-mono);
}

label {
  margin: 0;
  padding: 0;
}

label, h5 {
  font-size: 0.875rem;
}

fieldset {
  margin: 0;
  padding: 0;
  border: none;
}

input[type=number] {
  margin: 0;
  padding: 0.4375rem 1.125rem 0.3125rem 1.75rem;
  border: none;
  border-radius: 0.5rem;
  color: var(--very-dark-cyan);
  font-size: 1.5rem;
  text-align: end;
  font-family: inherit;
  font-weight: inherit;
}

input[type=number]::-webkit-inner-spin-button {
  appearance: none;
  margin: 0;
}

button {
  font-family: inherit;
  font-weight: inherit;
}

h1,
h2,
h3,
h4,
h5 {
  margin-top: 0;
  margin-bottom: 0;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

button {
  border: none;
  background: none;
}

label {
  margin-top: 0;
  margin-bottom: 0;
}

body {
  min-height: 100vh;
  background: var(--light-grayish-cyan);
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 3.125rem;
}

.calculator {
  background: var(--white);
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  padding: 2rem 1.5rem;
  border-radius: 1.5625rem 1.5625rem 0 0;
}
.calculator__form {
  padding: 0 0.5rem;
}

.title {
  color: var(--very-dark-cyan);
  display: flex;
  flex-direction: column;
  text-align: center;
  font-size: 0.6875rem;
  letter-spacing: 0.625rem;
  margin-bottom: 2.5625rem;
}

.form {
  display: flex;
  flex-direction: column;
  color: var(--dark-grayish-cyan);
  font-weight: 700 !important;
}
.form__bill, .form__tip {
  margin-bottom: 2.4375rem;
}
.form__bill {
  display: flex;
  flex-direction: column;
}
.form__bill-title {
  margin-bottom: 0.5rem;
}
.form__bill-input {
  background: no-repeat scroll 16px 16px url(../images/icon-dollar.svg), var(--lighter-grayish-cyan);
}
.form__tip {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 1.375rem repeat(3, 1fr);
  gap: 1rem;
  align-items: center;
}
.form__tip-title {
  grid-column: 1/3;
}
.form__tip-value {
  background: var(--very-dark-cyan);
  width: 9.125rem;
  height: 3rem;
  border-radius: 0.5rem;
  color: var(--lighter-grayish-cyan);
  font-size: 1.5rem;
}
.form__tip-value#tip-value-custom {
  background: var(--lighter-grayish-cyan);
}
.form__tip-value#tip-value-custom::placeholder {
  color: var(--dark-grayish-cyan);
}